import React from 'react';
import data from './css/data.css'
const sertifikat =[{
  id: 0,
  instansi: 'Codepolitan',
  sertifikat: 'codepolitan.jpg',
  keahlian: 'HTML dan CSS Dasar'
}, {
  id: 1,
  instansi: 'FreeCodeCamp',
  sertifikat: 'fcc.jpg',
  keahlian: 'Designer Web Responsive'
}];

function ListSertifikat(){
  const listItem = sertifikat.map(item => 
      <div className="polaroid" key={item.id}>
        <img src={item.sertifikat} />
        <div className="pcontent">
          <p><b>{item.instansi}</b></p>
          <p>{item.keahlian}</p>
        </div>
      </div>
  );
  
  return(
    <section>{listItem}</section>
  );
}
export default ListSertifikat;
